<script>
export default {
  data() {
    return {
      form2: {
        student_id: "",
        grade_xp: "",
        b1: "",
        b2: "",
        b3: "",
        b4: "",
        b5: "",
        b6: "",
        b7: "",
        c1: "",
        c2: "",
        c3: "",
        c4: "",
        c5: "",
        c6: "",
        c7: "",
        c8: "",
        c9: "",
        c10: "",
        c11: "",
        c12: "",
        d1: "",
        d2: "",
        d3: "",
        d4: "",
        d5: "",
        d6: "",
        e1: "",
        e2: "",
        e3: "",
        e4: "",
        f1: "",
        f2: "",
        f3: "",
        g3: "",
        g2: "",
        g1: "",
        x1: "",
        x2: "",
        x3: "",
      },
      form: {},
      tableData: [
        {
          tongdao: "",
          ch1: "CH1",
          ch2: "CH2",
        },
        {
          tongdao: "",
          ch1: "1",
          //  ch2:"1"
        },
        {
          tongdao: "",
          ch1: "2",
          ch2: "2",
        },
        {
          tongdao: "(ms)",
          ch1: "3",
          ch2: "3",
        },
        {
          tongdao: "(Hz)",
          ch1: "4",
          ch2: "4",
        },
      ],
      tableData1: [
        {
          hang1: "(ms/DIV)",
          hang2: "0.5",
          hang3: "0.2",
          hang4: "0.1",
        },
        {
          hang1: "(Hz)",
          hang2: "500",
          hang3: "1000",
          hang4: "2000",
        },
        {
          hang1: "(Hz)",
          hang2: "1",
          hang3: "1",
          hang4: "1",
        },
        {
          hang1: "",
          hang2: "2",
          hang3: "2",
          hang4: "2",
        },
        {
          hang1: "(ms)",
          hang2: "3",
          hang3: "3",
          hang4: "3",
        },
        {
          hang1: "(Hz)",
          hang2: "4",
          hang3: "4",
          hang4: "4",
        },
      ],
      tableData2: [
        {
          h1: "fy / fx",
          h2: "2/1",
          h3: "1/1",
          h4: "1/2",
        },
        {
          h1: "",
          h2: "1",
          h3: "1",
          h4: "1",
        },
        {
          h1: "fy(Hz)",
          h2: "2",
          h3: "2",
          h4: "2",
        },
        {
          h1: "fx(Hz)",
          h2: "3",
          h3: "3",
          h4: "3",
        },
      ],
    };
  },
  created() {
    this.form2.student_id = window.sessionStorage.getItem("student_id");
    // console.log(this.form2.student_id);
  },
  methods: {
    arraySpanMethod({ row, column, rowIndex, columnIndex }) {
      if (rowIndex === 1) {
        if (columnIndex === 1) {
          return [1, 2];
        }
      }
    },

    async send() {
      // console.log(this.form2.student_id);
      const answer1 = (
        this.form2.e1 +
        this.form2.e2 +
        this.form2.e3 +
        this.form2.e4 +
        this.form2.f1 +
        this.form2.f2 +
        this.form2.f3 +
        this.form2.g1 +
        this.form2.g2 +
        this.form2.g3
      ).split("");
      const answer2 = ["B", "E", "F", "A", "D", "A", "C", "B", "D", "G"];
      var mark1 = 0;
      for (let i = 0; i < answer1.length; i++) {
        if (answer1[i] == answer2[i]) {
          mark1 = mark1 + 2;
        }
      }
      const answer3 = (this.form2.x1 + this.form2.x2 + this.form2.x3).split("");
      const answer4 = ["0", "1", "0"];
      var mark2 = 0;
      for (let i = 0; i < answer3.length; i++) {
        if (answer3[i] == answer4[i]) {
          mark2 = mark2 + 4;
        }
      }
      var mark = 0;
      mark = mark1 + mark2;
      this.form2.student_id = window.sessionStorage.getItem("student_id");
      this.form2.grade_xp = mark;
      const { data: res } = await this.$http.post(
        "/api/experiment14/completion14",
        this.form2
      );
      if (res.code == 200) {
        this.$message.success('')
        this.$router.replace('/login')
      } else {
        this.$message.error(res.msg);
      }
    },
  },
};
</script>

