<template>
  <div class="zhuye">
    <div class="img">
      <img class="imgs" src="https://z3.ax1x.com/2021/06/30/RDF0RH.png" />
    </div>
    <div class="main">
      <div class="middle">
        <el-table
          :data="tableData"
          :span-method="arraySpanMethod"
          :show-header="showHeader"
          border
          style="width: 100%"
        >
          <el-table-column prop="id" width="140" label="">
          </el-table-column>
          <el-table-column prop="name">
            <template slot-scope="scope">
              <el-form
                :model="form"
                :rules="rules"
                ref="form"
                class="demo-ruleForm"
              >
                <el-select
                  v-model="form.experiment_name"
                  placeholder=""
                  v-if="scope.row.id == ''"
                  style="width100%;display:block;"
                >
                  <el-option
                    v-for="item in options"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value"
                  >
                  </el-option>
                </el-select>
                <!-- </el-form-item> -->
                <div v-if="scope.row.id == ''">
                  <el-input v-model="form.course_name"></el-input>
                </div>
                <div v-if="scope.row.id == ''">
                  <el-input v-model="form.student_spec"></el-input>
                </div>
                <!-- <div v-if="scope.row.id == ''"> -->
                <el-select
                  v-model="form.student_class"
                  placeholder=""
                  v-if="scope.row.id == ''"
                  style="width100%;display:block;"
                >
                  <el-option
                    v-for="item in options2"
                    :key="item.class"
                    :label="item.class"
                    :value="item.class"
                  >
                  </el-option>
                </el-select>
                <!-- <el-input v-model="form.student_class"></el-input> -->
                <!-- <el-cascader
            v-model="form.student_class"
            :options="options2"
            placeholder=""
          ></el-cascader>
                </div> -->
                <div v-if="scope.row.id == ''">
                  <el-input v-model="form.student_name"></el-input>
                </div>
                <div v-if="scope.row.id == ''">
                  <el-input v-model="form.student_teacher"></el-input>
                </div>
              </el-form>
            </template>
          </el-table-column>
          <el-table-column prop="amount1" width="140px"> </el-table-column>
          <el-table-column prop="amount2">
            <template slot-scope="scope">
              <el-form
                :model="form"
                :rules="rules"
                ref="form"
                class="demo-ruleForm"
              >
                <div v-if="scope.row.amount1 == ''">
                  <el-input v-model="form.student_level"></el-input>
                </div>
                <div v-if="scope.row.amount1 == ''">
                  <el-select
                    v-model="form.student_year"
                    placeholder=""
                    style="width100%;display:block;"
                  >
                    <el-option
                      v-for="item in optionss"
                      :key="item.value"
                      :label="item.label"
                      :value="item.value"
                    >
                    </el-option>
                  </el-select>
                  <!-- <el-cascader
            v-model="form.student_year"
            :options="optionss"
            placeholder=""
          ></el-cascader> -->
                </div>
                <div v-if="scope.row.amount1 == ''">
                  <el-input v-model="form.student_num"></el-input>
                </div>
                <div v-if="scope.row.amount1 == ''">
                  <el-input v-model="form.student_date"></el-input>
                </div>
              </el-form>
            </template>
          </el-table-column>
        </el-table>
        <el-form ref="form">
          <el-form-item style="text-align: center">
            <el-button
              class="btn"
              style="text-align: center"
              type="primary"
              @click="login()"
              ></el-button
            >
          </el-form-item>
        </el-form>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      form: {
        experiment_name: "",
        course_name: "",
        student_spec: "",
        student_class: "",
        student_name: "",
        student_teacher: "",
        student_level: "",
        student_year: "",
        student_num: "",
        student_date: "",
      },
      rules: {
        test_name: [
          {
            required: true,
            message: "",
            trigger: "blur",
          },
          {
            min: 1,
            max: 15,
            message: " 1  15 ",
            trigger: "blur",
          },
        ],
      },
      options2: [
        // {
        //   value: "",
        //   label: "",
        // },
        // {
        //   value: "",
        //   label: "",
        // },
        // {
        //   value: "",
        //   label: "",
        // },
      ],
      optionss: [
        {
          value: "2020",
          label: "2020",
        }
      ],
      options: [
        // {
        //   value: "",
        //   label: "",
        // },
        // {
        //   value: "",
        //   label: "",
        // },
        // {
        //   value: "",
        //   label: "",
        // },
        {
          value: "",
          label: "",
        },
        // {
        //   value: "",
        //   label: "",
        // },
        {
          value: "",
          label: "",
        },
        // {
        //   value:"",
        //   label:""
        // },
        // {
        //   value: "",
        //   label: "",
        // },
        {
          value: "",
          label: "",
        },
        {
          value: "",
          label: "",
        },
        {
          value: "",
          label: "",
        },
        //  {
        //   value: "",
        //   label: "",
        // },
      ],
      showHeader: false,
      checked: "",
      tableData: [
        {
          id: "",
          name: "",
        },
        {
          id: "",
          name: "",
          amount1: "",
          amount2: "",
        },
        {
          id: "",
          name: "",
          amount1: "",
          amount2: "",
        },
        {
          id: "",
          name: "",
          amount1: "",
          amount2: "",
        },
        {
          id: "",
          name: "",
          amount1: "",
          amount2: "",
        },
        {
          id: "",
          name: "",
        },
      ],
    };
  },
  created() {
    this.getClass();
  },
  methods: {
    login() {
      // console.log(this.form);
      this.$refs.form.validate(async (valide) => {
        const { data: res } = await this.$http.post(
          "api/experiment/student",
          this.form
        );
        // console.log(res);
        if (res.code == 200) {
          // console.log(res);
          if (res.data.state == 2) {
            this.$message.error("");
            return;
          }
          const number = this.form.student_num;
          window.sessionStorage.setItem("student_id", res.data.id);
          if (this.form.experiment_name == "") {
            this.$router.replace({
              name: "test",
              params: { number },
            });
          } else if (this.form.experiment_name == "") {
            this.$router.replace({
              name: "test1",
              params: { number },
            });
          } else if (this.form.experiment_name == "") {
            this.$router.replace({
              name: "test2",
              params: { number },
            });
          } else if (this.form.experiment_name == "") {
            this.$router.replace({
              name: "test3",
              params: { number },
            });
          } else if (this.form.experiment_name == "") {
            this.$router.replace({
              name: "test4",
              params: { number },
            });
          } else if (this.form.experiment_name == "") {
            this.$router.replace({
              name: "test5",
              params: { number },
            });
          } else if (this.form.experiment_name == "") {
            this.$router.replace({
              name: "test6",
              params: { number },
            });
          } else if (this.form.experiment_name == "") {
            this.$router.replace({
              name: "test7",
              params: { number },
            });
          }else if (this.form.experiment_name == "") {
            this.$router.replace({
              name: "test8",
              params: { number },
            });
          }  else if (this.form.experiment_name == "") {
            this.$router.replace({
              name: "test9",
              params: { number },
            });
          } else if (this.form.experiment_name == "") {
            this.$router.replace({
              name: "test10",
              params: { number },
            });
          }
           else if (this.form.experiment_name == "") {
            this.$router.replace({
              name: "testa",
              params: { number },
            });
          }
        }
      });
    },
    // login(){
    //     window.sessionStorage.setItem('student_id',this.form.student_num)
    //       this.$router.push({
    //         name:'logined',
    //         params: { number }
    //       });
    // },
    async getClass() {
      const { data: res } = await this.$http.get("/api/menu/getclass");
      this.options2 = res.data;
      // console.log(res.data);
    },
    arraySpanMethod({ row, column, rowIndex, columnIndex }) {
      if (rowIndex === 0) {
        if (columnIndex === 1) {
          return [1, 3];
        }
      } else if (rowIndex === 5) {
        if (columnIndex === 1) {
          return [1, 3];
        }
      }
    },
  },
};
</script>

<style>
.el-table td,
.el-table th {
  padding: 6px 0 !important;
}
.zhuye {
  height: 100%;
  background-color: #dee5f3;
}
.main {
  height: 60%;
  width: 50%;
  min-width: 300px;
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  margin: auto;
  display: flex;
  justify-content: center;
  align-items: center;
}

.middle {
  min-width: 250px;
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  margin: auto;
}
.btn {
  margin-top: 50px !important;
  background-color: #6f8ed3;
  text-align: center;
}
.img {
  height: 50px;
  background-color: #4168c0;
}
.imgs {
  height: 38px;
  padding: 6px 0 6px 10px;
}
</style>