    };
  },
  created() {
    this.form2.student_id = window.sessionStorage.getItem("student_id");
    // console.log(this.form2.student_id);
  },
  methods: {
    arraySpanMethod({ row, column, rowIndex, columnIndex }) {
      if (rowIndex === 0) {
        if (columnIndex === 7) {
          return [1, 2];
        } else if (columnIndex === 8) {
          return [1, 2];
        }
      } else if (rowIndex === 4) {
        if (columnIndex === 2) {
          return [1, 7];
        }
      } else if (rowIndex === 5) {
        if (columnIndex === 2) {
          return [1, 7];
        }
      } else if (rowIndex === 6) {
        if (columnIndex === 2) {
          return [1, 7];
        }
      }
    },
    arraySpanMethod1({ row, column, rowIndex, columnIndex }) {
      if (rowIndex === 0) {
        if (columnIndex === 3) {
          return [1, 2];
        }
      } else if (rowIndex === 1) {
        if (columnIndex === 0) {
          return [4, 1];
        }
      } else if (rowIndex === 5) {
        if (columnIndex === 0) {
          return [2, 1];
        }
      }
    },

    async send() {
      // console.log(this.form2.student_id);
      const answer1 = (this.form2.xz1 + this.form2.xz2).split("");
      const answer2 = ["A", "D"];
      var mark = 0;
      for (let i = 0; i < answer1.length; i++) {
        if (answer1[i] == answer2[i]) {
          mark = mark + 5;
        }
      }
      this.form2.student_id = window.sessionStorage.getItem("student_id");
      // console.log(this.form2);
      this.form2.grade_xp = mark;
      const { data: res } = await this.$http.post(
        "api/pendulum/completion3",
        this.form2
      );
      if (res.code == 200) {
        this.$message.success('')
        this.$router.replace('/login')
      } else {
        this.$message.error(res.msg);
      }
    },
  },
};
