<script>
export default {
  data() {
    return {
      form2: {
        student_id: "",
        grade_xp: "",
        ig: "",
        rg: "",
        i: "",
        rs: "",
        ix: "",
        vm: "",
        rf: "",
        vx: "",
        xz1: "",
        xz2: "",
        xz3: "",
        xz4: "",
      },
      form: {},
      tableData: [
        {
          sheji1: "",
        },
        {
          sheji1: "",
          sheji2: "	 I =",
          sheji3: "1",
          sheji4: "mA",
        },
        {
          sheji1: "",
          sheji2: "",
          sheji3: "Rs =",
          sheji4: "1",
          sheji5: "",
        },
        {
          sheji1: "",
          sheji2: "Ix =",
          sheji3: "2",
          sheji4: "mA",
        },
      ],
      tableData1: [
        {
          sheji1: "",
        },
        {
          sheji1: "",
          sheji2: " VM=	",
          sheji3: "1",
          sheji4: "V",
        },
        {
          sheji1: "",
          sheji2: "",
          sheji3: "Rf =",
          sheji4: "1",
          sheji5: "",
        },
        {
          sheji1: "",
          sheji2: "Ix =",
          sheji3: "2",
          sheji4: "V",
        },
      ],
    };
  },
  created() {
    this.form2.student_id = window.sessionStorage.getItem("student_id");
    // console.log(this.form2.student_id);
  },
  methods: {
    arraySpanMethod({ row, column, rowIndex, columnIndex }) {
      if (rowIndex === 0) {
        if (columnIndex === 0) {
          return [1, 5];
        }
      } else if (rowIndex === 1) {
        if (columnIndex === 1) {
          return [1, 2];
        }
      } else if (rowIndex === 3) {
        if (columnIndex === 0) {
          return [1, 2];
        }
      }
    },

    async send() {
      const answer1 = (
        this.form2.xz1 +
        this.form2.xz2 +
        this.form2.xz3 +
        this.form2.xz4
      ).split("");
      const answer2 = ["B", "A", "E", "D"];
      var mark = 0;
      for (let i = 0; i < answer1.length; i++) {
        if (answer1[i] == answer2[i]) {
          mark = mark + 10;
        }
      }
      this.form2.student_id = window.sessionStorage.getItem("student_id");
      //  console.log(this.form2);
      this.form2.grade_xp = mark;
      const { data: res } = await this.$http.post(
        "/api/experiment10/experiment10",
        this.form2
      );
      // console.log(res);
      if (res.code == 200) {
        this.$message.success("");
         this.$router.replace('/login')
        // window.location.href =
        //   "http://120.55.71.155/api/experiment10/pdf10?student_id=" +
        //   this.form2.student_id;
      } else {
        this.$message.error(res.msg);
      }
    },
  },
};
</script>

